#header{
  width: 100%;
  height:600px;
  background-color: antiquewhite;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow:hidden;
}
.Desk{
  width:100%;
  opacity: 0.6;
}
.Me{
  position: absolute;
  border-radius: 50%;
  width: 250px;
  top:680px;
  left: 0;
  opacity: 0;
  animation: me 1s forwards;
  animation-delay: 1.5s;
  filter: grayscale(1);
}
@keyframes me {
  to{opacity: 1;}
}

#headerHeader{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
  position: fixed;
  top:0;
  background-color:rgb(252, 196, 122,0.2);
  cursor: pointer;
  z-index: 1000;
}
#headerHome{
  display: flex;
  justify-content: center;
  align-items: center;
  width:20%;
  height:50px;
  margin-right: 50%;
  font-size: 60px;
  font-weight: bolder;
  transition: 0.3s;
  animation: up 1s infinite;
  text-shadow: 1px 1px 1px gray; 
  animation-play-state: paused;
}
@keyframes up{
  to{transform: translateY(-5px);}
}
#headerHome:hover{
  background-color: rgb(207, 207, 207,0.2);
  font-size: 63px;
  color:rgb(69, 69, 69);
}
.Header_Button{
  width:5%;
  height:35px;
  margin-right: 10px;
  font-size: 30px;
  text-align: center;
  transition: 0.3s;
  font-weight: bolder;
}
.Header_Button:hover{
 border-bottom:2px solid rgb(33, 33, 33);
 color:rgb(33, 33, 33);
 font-size: 33px;
}
.Header_Article1{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top:7%;
  width:55%;
  height:150px;
  text-align: center;
  font-size: 50px;
  position: absolute;
  color:rgb(249, 234, 214,0.8);
  text-shadow: 1px 1px 1px gray; 
  animation:textshadow 1s alternate infinite;
}
@keyframes textshadow{
  to{text-shadow: 3px 3px 3px gray;color:rgb(246, 224, 196);}
}
.Header_Article2{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top:2%;
  width:55%;
  height:800px;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  position: absolute;
  color:transparent;
  text-shadow: 1px 1px 1px gray;
  animation:text 1s forwards;
}
@keyframes text{
  to{color:black;}
}

