#profile{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width:auto;
  height:400px;
  background-color: rgb(254, 239, 217);
  font-weight:bold;
  text-shadow: 1px 1px 1px gray; 
}
.Profile{
  display: flex;
  justify-content: center;
  align-items: center;
  width:80%;
  height: 100px;
  font-size: 60px;
  font-weight:bolder;
  background-color: rgb(31, 31, 31, 0.9);
  color:rgb(248, 223, 190, 0.4);
  border-radius: 3%;
  box-shadow: 4px 4px 4px 4px gray;
  text-shadow: 1px 1px 1px gray; 
  animation: colorchange 0.5s infinite alternate;
}
@keyframes colorchange{
  to {color:rgb(248, 223, 190); font-size:62px;}
}
@keyframes slide {
  from{transform:translateX(-1000px)}
  to {transform:translateX(0px)}
}
.Profiles{
  display: flex;
  justify-content: center;
  align-items: center;
  width:25%;
  margin: 20px;
  height:70px;
  background-color: rgb(31, 31, 31, 0.9);
  color:rgb(248, 223, 190);
  border-radius: 3%;
  font-size: 28px;
  box-shadow: 3px 3px 3px 3px gray;
  animation: slide 0.3s;
}
.Profile_Name{
  animation: slide 1.4s;
}
.Profile_Phone{
  animation: slide 1.4s;
}
.Profile_Birth{
  animation: slide 1s;
}
.Profile_Mail{
  animation: slide 1s;
}
