.Project1{
  width:100%;
  height:7300px;
  background-color: rgb(248, 235, 217);
  overflow: hidden;
  padding-top: 3%;

}
.Back_Black{
  position: absolute;

  transform: translateY(300px);
  opacity: 0.7;
}
.Project_Container{
  display: none;
  left:0%;
  z-index: 1;
  transition-property:opacity;
  transition-duration:0.1s;
}

.Project_Container_Left1{
  position:fixed;
  width:100%;
  bottom:50%;
}
.Project_Container_Left2{
  position:fixed;
  width:100%;
  bottom:50%;
  
}
.Project_Container_Left3{
  position:fixed;
  width:100%;
  bottom:50%;
 
}
.Project_Container_Left4{
  position:fixed;
  width:100%;
  bottom:50%;
 
}

.Project_Img1{
  width:38%;
  margin: 2%;
  border-radius: 3%;
  box-shadow: 5px 5px 5px 5px gray;
}

.Project_Article{
  width:100%;
  height:100%;
  background-color: rgb(248, 235, 217);
  position: relative;
  top: 4120px;
  opacity: 0;
  transition: 0.2s;
}
.Project_Article img{
  width:100%;
}
#skills{
  margin-top: 100px;
  width:100%;
  height:1300px;
  font-size: 30px;
}
.Back{
  position: absolute;
  opacity: 0.9;
  transform: translateY(-100px);
}
#leftSkills{
  position: relative;
  float: left;
  text-align: center;
  width:8%;
  background-color: rgb(196, 205, 247);
  margin-right: 7px;
  z-index: 10;
}
#leftSkills img{
  margin-top:50px;
  width:60%;
  transition: 1s;
  z-index: 100;
}
#leftSkills img:hover{
  transform:rotate(360deg)
}
#rightSkills{
  width:90%;
  height:60%;
  display: flex;
  align-items: center;
  overflow: hidden;
}
#rightSkills div{
position: relative;
 width:100%;
 text-align: center;
 transition: 0.5s;
 transform: translateX(-50%);
 z-index: 100;
}
#rightSkills div:hover{
  transform: translateX(0%);
  
}
#rightSkills div img{
  margin-right:1%;
  width:45%;
  border-radius: 5%;
  animation:wave 1s infinite alternate;
  border: 3px solid rgb(248, 235, 217);
 }
 @keyframes wave {
  to{
    transform: translateX(1%);
  }
 }
 
#rightSkillsArticle{
  float: left;
  width:100%;
  height: 300px;
  background-color: aqua;
}
#gamecolorArticle{
  width: 100%;
  height:450px;
  display: flex;
  padding-bottom: 100px;
  
}
#gamecolorArticle div{
  width:50%;
  height: 100%;
  margin-left:50px;
  margin-right:50px;
  border-radius: 5%;
  box-shadow: 2px 2px 2px 2px grey inset;
  background-color: rgb(244, 237, 227);
  padding-left: 10px;
}
#skillsArticle{
  position:absolute;
  width:45%;
  height: 350px;
  right: 40px;
  transform: translateY(-550px);
  text-align: center;
  padding-top: 10px;
  border: 3px solid rgb(248, 235, 217);
  border-radius: 4%;
  background-color: rgb(248, 235, 217,0.2);
  color:rgb(255, 255, 255);
}
#skillsArticle p{
  font-weight: bolder;
  z-index: 1;
  text-decoration-line: none;
  font-size: larger;
  color: rgb(85, 253, 197);
}
#skillsArticle a{
  color: antiquewhite;
}
