#footer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  position: fixed;
  bottom:0;
  width:100%;
  transition: 0.5s;
}
.Footer_Icon{
  width:35px;
  height:35px;
  margin: 0px;
  margin-right:5px ;
  margin-left:5px ;
}
.Footer_Icon img{
  width: 100%;
  border-radius: 10%;
  cursor: pointer;
  filter: grayscale(1);
}
.Footer_Copyright{
  display: flex;
  justify-content: center;
  align-items: center;
  width:100%;
  height:30px;
  font-size: 25px;
}
.Design{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.Photoshop{
  width: 200px;
  height:200px;
  overflow: hidden;
  margin:3%;
}
.Photoshop:hover{
  width: 200px;
  height:200px;
  overflow:visible
}

.Photoshop_Img{
  width: 200px;
  height:200px;
  border:1px solid black;
}
.Photoshop_Img:hover{
  width: 200px;
  height:200px;
}
